export default ()=> {
      return (
        <div className="question" data-index="143" dangerouslySetInnerHTML = {{ __html: `
<h6>143. 哪一个选项会导致报错？</h6>
<pre class="language-javascript"><code class="language-javascript"><span class="token keyword keyword-const">const</span> emojis <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token string">"🎄"</span><span class="token punctuation">,</span> <span class="token string">"🎅🏼"</span><span class="token punctuation">,</span> <span class="token string">"🎁"</span><span class="token punctuation">,</span> <span class="token string">"⭐"</span><span class="token punctuation">]</span><span class="token punctuation">;</span>

<span class="token comment">/* 1 */</span> emojis<span class="token punctuation">.</span><span class="token function">push</span><span class="token punctuation">(</span><span class="token string">"🦌"</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">/* 2 */</span> emojis<span class="token punctuation">.</span><span class="token function">splice</span><span class="token punctuation">(</span><span class="token number">0</span><span class="token punctuation">,</span> <span class="token number">2</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token comment">/* 3 */</span> emojis <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token operator">...</span>emojis<span class="token punctuation">,</span> <span class="token string">"🥂"</span><span class="token punctuation">]</span><span class="token punctuation">;</span>
<span class="token comment">/* 4 */</span> emojis<span class="token punctuation">.</span>length <span class="token operator">=</span> <span class="token number">0</span><span class="token punctuation">;</span>
</code></pre>
<ul>
<li>A: 1</li>
<li>B: 1 and 2</li>
<li>C: 3 and 4</li>
<li>D: 3</li>
</ul>
<details><summary><b>答案</b></summary>
<p>
<h4>答案: D</h4>
<p><code>const</code> 关键字意味着我们不能 <em>重定义</em> 变量中的值，它 <em>仅可读</em>。而然，值本身不可修改。数组 <code>emojis</code> 中的值可被修改，如 push 新的值, 拼接，又或者将数组的长度设置为0。</p>
</p>
</details>
` }}></div>
      );
    }